
import { defineComponent } from "vue";
import { ElMessageBox } from "element-plus";
import AlcHeader from "./components/AlcHeader.vue";
import AlcAside from "./components/AlcAside.vue";
import AlcFooter from "./components/AlcFooter.vue";
import AlcFrame from "./components/AlcFrame.vue";

export default defineComponent({
  name: "App",
  components: {
    AlcHeader,
    AlcAside,
    AlcFooter,
    AlcFrame,
  },
  data(): Record<string, never> {
    return {};
  },
  created(): void {
    if (!sessionStorage.getItem("alertConfirmed")) {
      ElMessageBox.alert(
        "请勿尝试使用异常或过大的参数初始化模型！可能导致你的浏览器冻结甚至设备死机！",
        "重要提示",
        {
          confirmButtonText: "知道了",
        }
      );
      sessionStorage.setItem("alertConfirmed", "true");
    }
  },
});
